;IACZ,kBAAkB;IAClB,gBAAgB;IAChB,2CAA2C;IAC3C,mBAAmB;IACnB,cAAc;IACd,UAAU;AACd;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,UAAU;AACd;AAEA;IACI,gBAAgB;IAChB,yCAAyC;IACzC,mBAAmB;IACnB,cAAc;AAClB;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,2CAA2C;IAC3C,mBAAmB;IACnB,cAAc;AAClB;AAEA;IACI,WAAW;IACX,YAAY;IACZ,cAAc;IACd,kBAAkB;IAClB,kBAAkB;IAClB,iBAAiB;AACrB;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,UAAU;IACV,aAAa;IACb,aAAa;IACb,kBAAkB;IAClB,+CAA+C;IAC/C,yBAAyB;IACzB,UAAU;IACV,cAAc;AAClB;AAEA;IACI,WAAW;AACf;AAEA;IACI,uBAAuB;AAC3B;AAEA;IACI,yBAAyB;IACzB,mBAAmB;AACvB;AAEA;IACI,kBAAkB;IAClB,iBAAiB;AACrB;AAEA;IACI,gBAAgB;IAChB,2CAA2C;IAC3C,mBAAmB;IACnB,UAAU;IACV,UAAU;AACd","sourcesContent":["<template>\n    <div class=\"select-text\">Select modules to {{props.screenType.toLocaleLowerCase()}}</div>\n    <div class=\"top-text-container\">\n        <span class=\"select-modules-checkbox mdi mdi-minus-box mdi-24px\"\n            v-if=\"enabledModules.length == checkedModules.length\" @click=\"setAllCheckStatus(false)\"></span>\n        <span class=\"select-modules-checkbox mdi mdi-plus-box mdi-24px\"\n            v-if=\"enabledModules.length != checkedModules.length\" @click=\"setAllCheckStatus(true)\"></span>\n        <span class=\"bold-number\">{{ checkedModules.length }}</span>\n        <span class=\"top-text-description\" style=\"color: #616161\"> of </span>\n        <span class=\"bold-number\">{{ items.length }}</span>\n        <span class=\"top-text-description\"> modules selected</span>\n    </div>\n    <div style=\"display: flex\">\n        <div class=\"treeview-card\">\n            <tree :nodes=\"treeViewNodes\" :config=\"config\" @nodeChecked=\"setSelectedModules\"\n                @nodeUnchecked=\"setSelectedModules\">\n                <template #before-input=\"{ node }\">\n                    <span v-if=\"node.type == 'module'\"\n                        :class=\"'mdi mdi-puzzle module-color icon-position' + (props.screenType=='Deploy' && node.state.disabled ? ' deploy-screen-disabled-icon' : '')\" />\n                    <span v-else\n                        :class=\"'mdi mdi-application module-color icon-position' + (props.screenType=='Deploy' && node.state.disabled ? ' deploy-screen-disabled-icon' : '')\" />\n                </template>\n                <template #after-input=\"{ node }\">\n                    <span class=\"warning-text\"\n                        v-if=\"props.screenType!='Export' && node.state.disabled && node.type == 'module' && node.isRepoAvailableForService\">(Deploy\n                        Settings is not provided!)</span>\n                    <span class=\"warning-text\"\n                        v-if=\"props.screenType!='Export' && node.state.disabled && node.type == 'module' && !node.isRepoAvailableForService\">(This\n                        module must include an entity to deploy!)</span>\n                </template>\n            </tree>\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport tree from \"vue3-treeview\";\nimport \"vue3-treeview/dist/style.css\";\nimport { computed, ref } from \"vue\";\nimport { IApplication, IModule } from \"../../../../../../domain/model/models\";\nimport IDeployScreenProps, { ITreeViewNode } from \"../../../../../../domain/presentation/dialogs/IDeployScreenProps\";\n\ninterface IData {\n    deployScreenProps: IDeployScreenProps;\n    items: Array<IModule | IApplication>;\n    reposServiceStatus: Array<{ repoId: number; exist: boolean }>;\n    screenType: \"Export\" | \"Deploy\";\n}\n\ntype ITreeViewData = Record<string, ITreeViewNode>;\nconst props = defineProps<IData>();\nconst items = ref<Array<IModule | IApplication>>(props.items.filter((item) => item.children && !!item.children.length));\nconst roots = items.value.map((item) => item.ID);\nconst treeViewNodes = ref<ITreeViewData>(props.deployScreenProps.moduleSelectionState);\n\nif (!Object.keys(treeViewNodes.value).length) {\n    items.value.forEach((item) => {\n        const isRepoAvailableForService = props.screenType == 'Deploy' ? isRepoAvailable(item) : true;\n\n        treeViewNodes.value[item.ID] = {\n            ID: item.ID,\n            text: item.name,\n            name: item.name,\n            type: item.objectType,\n            state: {\n                checked: props.screenType == 'Deploy' ? ((item.deployParameters ? item.deployParameters.gitServiceRepoId != 0 : false) && isRepoAvailableForService) : true,\n                disabled: props.screenType == 'Deploy' ? (item.deployParameters ? item.deployParameters.gitServiceRepoId == 0 : true) || !isRepoAvailableForService : false,\n            },\n            deployParameters: item.deployParameters!,\n            isRepoAvailableForService,\n        };\n    });\n}\n\nfunction isRepoAvailable(item: IModule | IApplication) {\n    if (item.objectType == \"application\") {\n        return true;\n    }\n\n    const anyEntity = item.children.some(i => i.objectType == \"model\" && i.modelType == \"entityDesigner\");\n    const isServiceExist = item.deployParameters\n        ? props.reposServiceStatus.some(i => i.repoId == item.deployParameters!.gitServiceRepoId && i.exist)\n        : false; //Needs review!\n\n    return anyEntity || isServiceExist;\n}\n\nconst checkedModules = computed(() =>\n    Object.keys(treeViewNodes.value)\n        .map((key) => treeViewNodes.value[key])\n        .filter((item) => item.state.checked)\n);\n\nconst enabledModules = computed(() =>\n    Object.keys(treeViewNodes.value)\n        .map((key) => treeViewNodes.value[key])\n        .filter((item) => !item.state.disabled)\n);\n\nconst config = ref({\n    roots,\n    checkboxes: true,\n    dragAndDrop: false,\n    editable: false,\n    checkMode: 0,\n    disabledClass: \"deploy-screen-disabled-node\",\n});\n\nfunction setSelectedModules() {\n    props.deployScreenProps.modulesChanged = true;\n    props.deployScreenProps.selectedModules = items.value.filter((item) =>\n        checkedModules.value.some((checkedModule) => checkedModule.ID === item.ID && checkedModule.state.checked)\n    );\n\n    props.deployScreenProps.selectedAssets = props.deployScreenProps.selectedModules.map(\n        (module) =>\n        ({\n            ID: module.ID,\n            name: module.name,\n            text: module.name,\n            type: module.objectType,\n            usageType: undefined,\n            children: module.children,\n            state: { checked: true },\n            version: undefined,\n            deployParameters: module.deployParameters,\n        } as ITreeViewNode)\n    );\n\n    props.deployScreenProps.stepper.isTabValid = !!props.deployScreenProps.selectedModules.length;\n}\n\nsetSelectedModules();\n\nfunction setAllCheckStatus(selected: boolean) {\n    Object.keys(treeViewNodes.value).forEach(\n        (key) => (treeViewNodes.value[key].state.checked = selected && !treeViewNodes.value[key].state.disabled)\n    );\n    setSelectedModules();\n}\n</script>\n\n<style scoped>\n@import url(\"../../../../../../styles/IDE3.css\");\n\n.select-text {\n    position: absolute;\n    top: 141px;\n    left: 62px;\n    width: 370px;\n    height: 26px;\n    /* UI Properties */\n    text-align: left;\n    font: normal normal normal 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #757575;\n    opacity: 1;\n}\n\n.top-text-container {\n    position: absolute;\n    top: 185px;\n    left: 62px;\n}\n\n.bold-number {\n    text-align: left;\n    font: normal normal bold 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #212121;\n}\n\n.top-text-description {\n    /* UI Properties */\n    text-align: left;\n    font: normal normal normal 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #757575;\n}\n\n.select-modules-checkbox {\n    width: 18px;\n    height: 18px;\n    color: #151233;\n    /* UI Properties */\n    border-radius: 50%;\n    margin-right: 7px;\n}\n\n.treeview-card {\n    position: absolute;\n    top: 219px;\n    left: 62px;\n    width: 1293px;\n    height: 392px;\n    /* UI Properties */\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #e0e0e0;\n    opacity: 1;\n    overflow: auto;\n}\n\n.treeview-card::-webkit-scrollbar {\n    width: 12px;\n}\n\n.treeview-card::-webkit-scrollbar-track {\n    background: transparent;\n}\n\n.treeview-card::-webkit-scrollbar-thumb {\n    background-color: #d0d1e9;\n    border-radius: 16px;\n}\n\n.icon-position {\n    margin-right: -8px;\n    margin-left: 10px;\n}\n\n.warning-text {\n    text-align: left;\n    font: normal normal normal 16px/21px Roboto;\n    letter-spacing: 0px;\n    color: red;\n    opacity: 1;\n}\n</style>\n"],"sourceRoot":""}]);
