 background: #ffffff 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    opacity: 1;\n}\n.previous-button-text[data-v-afd1ba48] {\n    cursor: pointer;\n    position: absolute;\n    top: 664px;\n    left: 1139px;\n    width: 78px;\n    height: 26px;\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26) var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    text-align: right;\n    font: normal normal normal 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #151233;\n    opacity: 1;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/deploy/deployScreen.vue"],"names":[],"mappings":";AAsPA;IACI,aAAa;IACb,aAAa;IACb,kBAAkB;AACtB;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,UAAU;IACV,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,+CAA+C;IAC/C,kBAAkB;IAClB,UAAU;AACd;AAEA;IACI,eAAe;IACf,YAAY;AAChB;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,UAAU;IACV,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,8KAA8K;IAC9K,kDAAkD;IAClD,mBAAmB;IACnB,gBAAgB;IAChB,2CAA2C;IAC3C,mBAAmB;IACnB,cAAc;IACd,UAAU;AACd;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,UAAU;IACV,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,8KAA8K;IAC9K,kDAAkD;IAClD,mBAAmB;IACnB,iBAAiB;IACjB,2CAA2C;IAC3C,mBAAmB;IACnB,cAAc;IACd,UAAU;AACd;AAEA;IACI,eAAe;AACnB;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,UAAU;IACV,YAAY;IACZ,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,oDAAoD;IACpD,+CAA+C;IAC/C,kBAAkB;IAClB,UAAU;AACd;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,UAAU;IACV,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,8KAA8K;IAC9K,kDAAkD;IAClD,iBAAiB;IACjB,2CAA2C;IAC3C,mBAAmB;IACnB,cAAc;IACd,UAAU;AACd","sourcesContent":["<script setup lang=\"ts\">\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IApplication, IModel, IModule } from '../../../../../domain/model/models';\nimport { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\nimport IDeployScreenProps from \"../../../../../domain/presentation/dialogs/IDeployScreenProps\";\nimport DeployScreenHeader from \"./deployScreenHeader.vue\";\nimport ModuleSelectionTab from \"./tabs/moduleSelectionTab.vue\";\nimport AssetSelectionTab from \"./tabs/assetSelectionTab.vue\";\nimport SummaryTab from \"./tabs/summaryTab.vue\";\nimport { onBeforeMount, ref } from \"vue\";\nimport { IDeployJobItem, IExportItem, IGitlabEnvironment } from \"@stechquick/algae/lib/qCloudTemp/exporter\";\nimport { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\nimport { IDeployJobs } from \"../../../../../domain/useCase/IDeployJobs\";\nimport { IJobUtils } from \"../../../../../domain/useCase/IJobUtils\";\nimport { ICreateExportJob, ICreateExportJobParam } from \"../../../../../domain/useCase/ICreateExportJob\";\nimport { CryptoHelper } from \"@stechquick/algae/lib/helpers/cryptoHelper\";\nimport { IPackJobs } from \"../../../../../domain/useCase/IPackJobs\";\n\ninterface IDeployScreenAdditionalProps {\n    screenType: \"Export\" | \"Deploy\";\n}\n\nconst emit = defineEmits([\"close\"]);\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst props = defineProps<IDeployScreenAdditionalProps>();\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst deployJobsUseCase = container.resolve<IDeployJobs>(IDeployJobs);\nconst packJobsUseCase = container.resolve<IPackJobs>(IPackJobs);\nconst jobUtils = container.resolve<IJobUtils>(IJobUtils);\nconst items = viewModel.studio.items.filter((item) => item.objectType == \"module\") as Array<IModule | IApplication>;\nconst currentApp: IApplication = viewModel.apps.find((item) => item.ID == viewModel.studio.appId)!;\ncurrentApp.children = viewModel.studio.items.filter((item) => item.objectType == \"model\") as Array<IModel>\n\nitems.unshift(currentApp);\n\nconst reposServiceStatus = ref<Array<{ repoId: number; exist: boolean }>>([]);\n\n\nonBeforeMount(async () => {\n    if (props.screenType == \"Export\") {\n        deployScreenProps.value.stepper.step = 1;\n        return;\n    }\n\n    await executor.execute(async () => {\n        const repoIds = items\n            .filter(i => i.deployParameters != undefined && i.deployParameters.gitServiceRepoId != 0)\n            .map(i => i.deployParameters!.gitServiceRepoId)\n            .filter((value, index, self) => self.indexOf(value) == index);\n\n        if (repoIds.length == 0) { return; }\n\n        const retVal = await deployJobsUseCase.checkRepoHasService(repoIds);\n        reposServiceStatus.value = retVal;\n        deployScreenProps.value.stepper.step = 1;\n    }, { loading: true });\n});\n\nconst deployScreenProps = ref<IDeployScreenProps>({\n    selectedModules: [],\n    moduleSelectionState: {},\n    modulesChanged: false,\n    selectedAssets: [],\n    assetSelectionState: {},\n    stepper: {\n        step: 0,\n        isTabValid: false,\n    },\n});\n\nfunction handleNext() {\n    if (deployScreenProps.value.stepper.isTabValid && deployScreenProps.value.stepper.step < 3) {\n        deployScreenProps.value.stepper.step++;\n        deployScreenProps.value.stepper.isTabValid = false;\n    }\n}\n\nfunction handlePrevious() {\n    if (deployScreenProps.value.stepper.step > 1) {\n        deployScreenProps.value.stepper.step--;\n        deployScreenProps.value.stepper.isTabValid = false;\n    }\n}\n\n// function getTypeOfModel(usageType: UsageType, type: ModelType, fileName: string): IExportItem[\"type\"] {\n//     if(usageType == \"appSettings\") {\n//         switch (fileName) {\n//             case \"pipeline\": return \"pipeline_qjson\";\n//             case \"alert\": return \"alert_qjson\";\n//             case \"loading\": return \"loading_qjson\";\n//             case \"containerServices\": return \"containerServices_js\";\n//             case \"settings\": return \"settings_yaml\";\n//         }\n//     }\n//     if(type == \"qjson\") return \"qjsons\";\n//     if(type == \"js\") return \"containerServices_js\";\n//     if(type == \"yaml\") return \"settings_yaml\";\n//     if(type == \"namedComponent\") return \"namedComps\";\n//     return type;\n// }\n\nfunction createJob() {\n    const groupID = CryptoHelper.CreateGuid();\n    packJob(groupID).then(e => {\n        if (props.screenType == \"Export\")\n            createExportJob(groupID);\n        // else if (props.screenType == \"Deploy\")\n        //     createDeployJob();\n    });\n    createDeployJob();\n}\n\nasync function packJob(groupID: string) {\n    let exportItemMap = new Map<string, Array<IExportItem>>();\n\n    deployScreenProps.value.selectedAssets.forEach((asset) => {\n\n        if (asset.state.checked) {\n            let checkedItems = Array<IExportItem>();\n            asset.children!.forEach(child => {\n                if (!child.state.checked || child.type == \"module\" || child.type == \"application\" || child.type == \"folder\")\n                    return;\n\n                checkedItems.push({\n                    id: child.ID,\n                    ver: child.version,\n                    type: jobUtils.getItemType(child),\n                    path: \"\",\n                    name: jobUtils.getFileFullname(child),\n                    ownerType: asset.type === 'module' ? 'module' : 'application',\n                    ownerName: asset.name\n                })\n            });\n            exportItemMap.set(asset.name, checkedItems);\n        }\n    })\n\n    let jobs = Array<ICreateExportJobParam>();\n    exportItemMap.forEach((v, k) => {\n        jobs.push({\n            app: { ID: viewModel.studio.appId, name: viewModel.studio.appName },\n            exportType: \"plateauBackend\",\n            selectionData: v,\n            layer: 0,\n            groupID: groupID\n        })\n    })\n\n    executor.execute(async () => {\n        await packJobsUseCase.createPackJobs(jobs);\n        emit(\"close\");\n    }, { loading: true })\n}\n\nasync function createExportJob(groupID: string) {\n    executor.execute(async () => {\n        await executor.executeUseCase<ICreateExportJob>(ICreateExportJob, {\n            app: { ID: viewModel.studio.appId, name: viewModel.studio.appName },\n            exportType: \"plateauBackend\",\n            selectionData: [],\n            layer: 1,\n            groupID: groupID\n        });\n        emit(\"close\");\n    },\n        { loading: true })\n}\n\nfunction createDeployJob() {\n    const deployJobs = Array<IDeployJobItem>();\n    deployScreenProps.value.selectedAssets.forEach((module) => {\n        const deployItems = Array<IExportItem>();\n\n        const environment: IGitlabEnvironment = {\n            type: \"gitlab\", params: {\n                repoId: module.deployParameters!.gitServiceRepoId, productRepoId: currentApp.deployParameters!.gitProductRepoId,\n                jobName: currentApp.deployParameters!.jobName, commitServiceId: currentApp.deployParameters!.commitServiceId\n            }\n        };\n\n        if (module.state.checked) {\n            module.children!.forEach((children) => {\n                if (children.type == \"module\" || children.type == \"application\" || children.type == \"folder\") return;\n\n                if (children.state.checked) {\n                    deployItems.push({\n                        id: children.ID,\n                        name: jobUtils.getFileFullname(children),\n                        type: jobUtils.getItemType(children),\n                        path: \"/\",\n                        ver: children.version,\n                    });\n                }\n            });\n            deployJobs.push({\n                deployType: module.type == \"application\" ? \"application\" : \"module\",\n                deployObject: { ID: module.ID, name: module.name },\n                deployItems,\n                environment,\n                dropServiceDb: true,\n                layer: 0,\n            });\n        }\n\n        //Open this line after \"product\" deployment developments\n        //deployJobs.push({ deployType: \"product\", deployItems: [], deployObject: { ID: \"\", name: \"\" }, dropServiceDb: false, environment, layer: 1, });\n\n    });\n    executor.execute(\n        async () => {\n            await deployJobsUseCase.createDeployJobs(deployJobs, false); //Set it \"true\" after \"product\" deployment developments\n            emit(\"close\");\n        },\n        { loading: true }\n    );\n}\n</script>\n\n<template>\n    <div class=\"deploy-screen-container\">\n        <DeployScreenHeader :step=\"deployScreenProps.stepper.step\" />\n        <ModuleSelectionTab :deploy-screen-props=\"deployScreenProps\" :items=\"items\"\n            :reposServiceStatus=\"reposServiceStatus\" v-if=\"deployScreenProps.stepper.step == 1\"\n            :screenType=\"props.screenType\" />\n        <AssetSelectionTab :deploy-screen-props=\"deployScreenProps\" :reposServiceStatus=\"reposServiceStatus\"\n            :screenType=\"props.screenType\" v-if=\"deployScreenProps.stepper.step == 2\" />\n        <SummaryTab :deploy-screen-props=\"deployScreenProps\" v-if=\"deployScreenProps.stepper.step == 3\" />\n\n        <div class=\"previous-button\" @click=\"handlePrevious\" v-if=\"deployScreenProps.stepper.step != 1\" />\n        <span class=\"previous-button-text\" @click=\"handlePrevious\"\n            v-if=\"deployScreenProps.stepper.step != 1\">Previous</span>\n\n        <div class=\"default-button\" data-v-33a93b5b=\"\" v-if=\"deployScreenProps.stepper.step == 3\" @click=\"createJob\" />\n        <span class=\"finish-button-text\" v-if=\"deployScreenProps.stepper.step == 3\" @click=\"createJob\"> FINISH </span>\n\n        <div :class=\"'default-button' + (deployScreenProps.stepper.isTabValid ? '' : ' button-disabled')\"\n            data-v-33a93b5b=\"\" @click=\"handleNext\" v-if=\"deployScreenProps.stepper.step < 3\" />\n        <span :class=\"'next-button-text' + (deployScreenProps.stepper.isTabValid ? '' : ' text-disabled')\"\n            v-if=\"deployScreenProps.stepper.step < 3\" @click=\"handleNext\">\n            NEXT\n        </span>\n    </div>\n</template>\n\n<style scoped>\n.deploy-screen-container {\n    width: 1417px;\n    height: 700px;\n    position: relative;\n}\n\n.default-button {\n    cursor: pointer;\n    position: absolute;\n    top: 652px;\n    left: 1257px;\n    width: 98px;\n    height: 48px;\n    /* UI Properties */\n    background: #151233 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    opacity: 1;\n}\n\n.button-disabled {\n    cursor: default;\n    opacity: 0.5;\n}\n\n.next-button-text {\n    cursor: pointer;\n    position: absolute;\n    top: 664px;\n    left: 1281px;\n    width: 50px;\n    height: 26px;\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26) var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--white);\n    text-align: left;\n    font: normal normal normal 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #ffffff;\n    opacity: 1;\n}\n\n.finish-button-text {\n    cursor: pointer;\n    position: absolute;\n    top: 664px;\n    left: 1275px;\n    width: 63px;\n    height: 26px;\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26) var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--white);\n    text-align: right;\n    font: normal normal normal 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #ffffff;\n    opacity: 1;\n}\n\n.text-disabled {\n    cursor: default;\n}\n\n.previous-button {\n    cursor: pointer;\n    position: absolute;\n    top: 652px;\n    left: 1115px;\n    width: 126px;\n    height: 48px;\n    /* UI Properties */\n    background: var(--white) 0% 0% no-repeat padding-box;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    opacity: 1;\n}\n\n.previous-button-text {\n    cursor: pointer;\n    position: absolute;\n    top: 664px;\n    left: 1139px;\n    width: 78px;\n    height: 26px;\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26) var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    text-align: right;\n    font: normal normal normal 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #151233;\n    opacity: 1;\n}\n</style>\n"],"sourceRoot":""}]);
