function createJob() {
    const groupID = CryptoHelper.CreateGuid();
    packJob(groupID).then(e => {
        if (props.screenType == "Export")
            createExportJob(groupID);
        // else if (props.screenType == "Deploy")
        //     createDeployJob();
    });
    createDeployJob();
}

async function packJob(groupID: string) {
    let exportItemMap = new Map<string, Array<IExportItem>>();

    deployScreenProps.value.selectedAssets.forEach((asset) => {

        if (asset.state.checked) {
            let checkedItems = Array<IExportItem>();
            asset.children!.forEach(child => {
                if (!child.state.checked || child.type == "module" || child.type == "application" || child.type == "folder")
                    return;

                checkedItems.push({
                    id: child.ID,
                    ver: child.version,
                    type: jobUtils.getItemType(child),
                    path: "",
                    name: jobUtils.getFileFullname(child),
                    ownerType: asset.type === 'module' ? 'module' : 'application',
                    ownerName: asset.name
                })
            });
            exportItemMap.set(asset.name, checkedItems);
        }
    })

    let jobs = Array<ICreateExportJobParam>();
    exportItemMap.forEach((v, k) => {
        jobs.push({
            app: { ID: viewModel.studio.appId, name: viewModel.studio.appName },
            exportType: "plateauBackend",
            selectionData: v,
            layer: 0,
            groupID: groupID
        })
    })

    executor.execute(async () => {
        await packJobsUseCase.createPackJobs(jobs);
        emit("close");
    }, { loading: true })
}

async function createExportJob(groupID: string) {
    executor.execute(async () => {
        await executor.executeUseCase<ICreateExportJob>(ICreateExportJob, {
            app: { ID: viewModel.studio.appId, name: viewModel.studio.appName },
            exportType: "plateauBackend",
            selectionData: [],
            layer: 1,
            groupID: groupID
        });
        emit("close");
    },
        { loading: true })
}

function createDeployJob() {
    const deployJobs = Array<IDeployJobItem>();
    deployScreenProps.value.selectedAssets.forEach((module) => {
        const deployItems = Array<IExportItem>();

        const environment: IGitlabEnvironment = {
            type: "gitlab", params: {
                repoId: module.deployParameters!.gitServiceRepoId, productRepoId: currentApp.deployParameters!.gitProductRepoId,
                jobName: currentApp.deployParameters!.jobName, commitServiceId: currentApp.deployParameters!.commitServiceId
            }
        };

        if (module.state.checked) {
            module.children!.forEach((children) => {
                if (children.type == "module" || children.type == "application" || children.type == "folder") return;

                if (children.state.checked) {
                    deployItems.push({
                        id: children.ID,
                        name: jobUtils.getFileFullname(children),
                        type: jobUtils.getItemType(children),
                        path: "/",
                        ver: children.version,
                    });
                }
            });
            deployJobs.push({
                deployType: module.type == "application" ? "application" : "module",
                deployObject: { ID: module.ID, name: module.name },
                deployItems,
                environment,
                dropServiceDb: true,
                layer: 0,
            });
        }

        //Open this line after "product" deployment developments
        //deployJobs.push({ deployType: "product", deployItems: [], deployObject: { ID: "", name: "" }, dropServiceDb: false, environment, layer: 1, });

    });
    executor.execute(
        async () => {
            await deployJobsUseCase.createDeployJobs(deployJobs, false); //Set it "true" after "product" deployment developments
            emit("close");
        },
        { loading: true }
    );
}

