palette yaml-color icon-position\" />\n                <span v-if=\"node.type == 'js'\" class=\"mdi mdi-palette js-color icon-position\" />\n                <span v-if=\"node.type == 'process'\" class=\"mdi mdi-auto-fix process-color icon-position\" />\n                <span v-if=\"node.type == 'bpmn'\" class=\"mdi mdi-vector-polyline bpmn-color icon-position\" />\n                <span v-if=\"node.type == 'entityDesigner'\"\n                    :class=\"'mdi mdi-database entity-designer-color icon-position' + (node.state.disabled ? ' deploy-screen-disabled-icon' : '')\" />\n                <span v-if=\"node.type == 'flow'\" class=\"mdi mdi-step-forward bpmn-color icon-position\" />\n            </template>\n            <template #after-input=\"{ node }\" style=\"display: flex; align-items: flex-end\">\n                <span class=\"warning-text\" v-if=\"node.state.disabled && props.screenType!='Export'  \">(Entity selection\n                    is required for this\n                    module!)</span>\n                <div class=\"col align-self-end\" v-if=\"node.version != undefined\" style=\"margin-right: 10px;\">\n                    <div class=\"pencilIcon\">\n                        <h6 class=\"align-version tree-text ps-3 versionText\">\n                            <span v-if=\"node.version == 'System latest'\" class=\"versionBadge latestVersionBadge\">*\n                            </span>\n                            {{ node.version + \" version\" }}\n                            <i class=\"mdi mdi-pencil-outline ms-1\" @click=\"versionDialog(node)\"></i>\n                        </h6>\n                    </div>\n                </div>\n                <div v-if=\"node.type == 'module' || node.type == 'application'\">\n                    <span class=\"selected-items-count\">\n                        (<span class=\"selected-items-count-bold\">\n                            {{ node.children.filter((children:any) => treeViewNodes[children].state.checked ).length}}\n                        </span>\n                        of\n                        <span class=\"selected-items-count-bold\">\n                            {{ node.children.length }}\n                        </span>\n                        models selected)\n                    </span>\n                </div>\n            </template>\n        </tree>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport tree from \"vue3-treeview\";\nimport \"vue3-treeview/dist/style.css\";\nimport { defineAsyncComponent, ref } from \"vue\";\nimport { container } from \"../../../../../../domain/core/diContainer\";\nimport IDeployScreenProps, { ITreeViewNode } from \"../../../../../../domain/presentation/dialogs/IDeployScreenProps\";\nimport { IDialog } from \"../../../../../../domain/presentation/IDialog\";\n\ninterface IData {\n    deployScreenProps: IDeployScreenProps;\n    reposServiceStatus: Array<{ repoId: number; exist: boolean; }>;\n    screenType: \"Export\" | \"Deploy\"\n}\n\ntype ITreeViewData = Record<string, ITreeViewNode>;\nconst props = defineProps<IData>();\nconst dialog = container.resolve<IDialog>(IDialog);\nconst selectedAssets = ref<Array<ITreeViewNode>>(props.deployScreenProps.selectedAssets);\nconst roots = selectedAssets.value.map((item) => item.ID);\nconst treeViewNodes = ref<ITreeViewData>(props.deployScreenProps.assetSelectionState);\n\nfunction iterateItemsAndFindChildren(items: Array<any>, parent?: any) {\n    items.forEach((item) => {\n        const selectionRequired = isSelectionRequired(item, parent);\n\n        const curNode: ITreeViewNode = (treeViewNodes.value[item.ID] = {\n            ID: item.ID,\n            text: item.name,\n            name: item.name,\n            type: item.type || item.modelType || item.objectType,\n            usageType: item.usageType,\n            state: { checked: true, opened: true, disabled: selectionRequired },\n            version: item.type != \"folder\" && item.type != \"module\" && item.type != \"application\" ? \"latest\" : undefined,\n            deployParameters: item.deployParameters\n        });\n        if (!item.children) {\n            return;\n        }\n\n        curNode.children = item.children.map((child: any) => child.ID);\n        iterateItemsAndFindChildren(item.children, item);\n    });\n}\n\nfunction isSelectionRequired(item: any, parent: any) {\n    const type = item.type || item.modelType || item.objectType;\n    if (props.screenType != \"Deploy\" || type != \"entityDesigner\") { return false; }\n\n    const isServiceExist = props.reposServiceStatus.some(i => i.repoId == parent.deployParameters!.gitServiceRepoId && i.exist);\n    return !isServiceExist;\n}\n\nif (!Object.keys(treeViewNodes.value).length || props.deployScreenProps.modulesChanged) {\n    props.deployScreenProps.selectedAssets.forEach((asset) => {\n        if (!Object.keys(treeViewNodes.value).includes(asset.ID)) {\n            iterateItemsAndFindChildren(selectedAssets.value);\n        }\n    });\n    props.deployScreenProps.modulesChanged = false;\n}\n\nselectedAssets.value.forEach((asset) => {\n    asset.children = asset.children?.map((child) => treeViewNodes.value[child.ID]);\n});\n\nconst config = ref({\n    roots,\n    checkboxes: true,\n    dragAndDrop: false,\n    editable: false,\n    checkMode: 0,\n});\n\nfunction setSelectedAssets() {\n    selectedAssets.value.forEach((asset) => {\n        asset.state.checked = asset.children!.some((children) => children.state.checked)\n        const entityChild = asset.children?.find(child => child.type == \"entityDesigner\" && child.state.disabled)\n        if (asset.state.checked && entityChild) {\n            entityChild.state.checked = true;\n        }\n    });\n    props.deployScreenProps.stepper.isTabValid = props.deployScreenProps.selectedAssets.some((asset) => asset.state.checked);\n}\n\nsetSelectedAssets();\n\nfunction versionDialog(clickedNode: ITreeViewNode) {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(\"../../../application/modelVersionDialog.vue\")),\n        { closable: true, title: \"Change Version\", width: \"900px\", height: \"auto\", overflow: \"auto\" },\n        { clickedNode }\n    );\n}\n</script>\n\n<style scoped>\n@import url(\"../../../../../../styles/IDE3.css\");\n\n.select-text {\n    position: absolute;\n    top: 141px;\n    left: 62px;\n    width: 370px;\n    height: 26px;\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26) var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--grey-darken-1);\n    text-align: left;\n    font: normal normal normal 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #757575;\n    opacity: 1;\n}\n\n.treeview-card {\n    position: absolute;\n    top: 182px;\n    left: 62px;\n    width: 1293px;\n    height: 429px;\n    /* UI Properties */\n    background: var(--white) 0% 0% no-repeat padding-box;\n    border: 1px solid var(--grey-lighten-2);\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #e0e0e0;\n    opacity: 1;\n    overflow: auto;\n}\n\n.treeview-card::-webkit-scrollbar {\n    width: 12px;\n}\n\n.treeview-card::-webkit-scrollbar-track {\n    background: transparent;\n}\n\n.treeview-card::-webkit-scrollbar-thumb {\n    background-color: #D0D1E9;\n    border-radius: 16px;\n}\n\n.pencilIcon {\n    text-align: end;\n}\n\n.selected-items-count {\n    font: normal normal normal 16px/21px Roboto;\n    color: #757575;\n    margin-left: 8px;\n}\n\n.selected-items-count-bold {\n    font: normal normal bold 16px/21px Roboto;\n    color: #151233;\n}\n\n.icon-properties {\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #00000000;\n    opacity: 0;\n}\n\n.icon-position {\n    margin-right: -8px;\n    margin-left: 10px;\n}\n\n.qjson-color {\n    color: #449dd1;\n}\n\n.yaml-color {\n    color: #449dd1;\n}\n\n.js-color {\n    color: #449dd1;\n}\n\n.process-color {\n    color: #007c77;\n}\n\n.bpmn-color {\n    color: #007c77;\n}\n\n.entity-designer-color {\n    color: #e9724c;\n}\n\n.warning-text {\n    text-align: left;\n    font: normal normal normal 16px/21px Roboto;\n    letter-spacing: 0px;\n    color: red;\n    opacity: 1;\n}\n</style>\n"],"sourceRoot":""}]);
